@import url("https://fonts.googleapis.com/css?family=Lato:100,100italic,300,300italic,regular,italic,700,700italic,900,900italic");
.pane {
  background: #e4fde1;
  margin: 2rem auto;
  max-width: 100%;
  border: 2px #666 solid;
  border-radius: 8px;
  box-shadow: 1px 1px 2px 2px #196d8a; }
  .pane .pane-titlebar {
    width: 100%;
    padding: 0.2rem 1rem;
    border-bottom: 1px solid #333;
    font-family: 'Lato', sans-serif;
    font-size: 0.7rem;
    background: #456990;
    color: #eee;
    box-shadow: 2px 2px -2px -2px #196d8a; }
    .pane .pane-titlebar * {
      display: inline; }
    .pane .pane-titlebar h2 {
      margin: 0;
      font-weight: 400; }
    .pane .pane-titlebar .pane-titlebar-icon-container {
      float: right; }
    .pane .pane-titlebar .pane-titlebar-icon {
      padding: 0.5rem 0.5rem;
      cursor: pointer; }
    .pane .pane-titlebar .pane-titlebar-icon-disabled {
      color: #a2a2a2; }
  .pane .pane-content-component {
    background: #e4fde1;
    width: 100%;
    padding-left: 1rem; }
    .pane .pane-content-component div {
      min-height: 50vh; }
    .pane .pane-content-component textarea {
      width: 100%;
      min-height: inherit;
      height: 100%;
      background: #e4fde1;
      padding: 0;
      border: none;
      resize: vertical; }

.pane-hidden {
  display: none; }

.pane-content-hidden {
  display: none; }

.pane-maximized .pane-content-component div {
  height: 90vh; }
  .pane-maximized .pane-content-component div textarea {
    height: 99%; }
